*{
  font-family: monospace;
}

body{
  padding: 60px;
}

input{
  width: 100%;
font-size: 1.2rem;
color: grey;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-top: 10px;
  margin-bottom: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;

}

.card img {
  max-height:170px ;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}